import Texture3D from '../components/visualization/engine/Texture3D';
import Volume from '../components/visualization/engine/Volume';
import VolumeSet from '../components/visualization/engine/VolumeSet';
import pako from 'pako';
const NEED_TEXTURE_SIZE_4X = true;

// eslint-disable-next-line no-restricted-globals
const ctx: Worker = self as any;
ctx.addEventListener("message", (event) => {

  if (event.data.data) {
    const blob: Blob = event.data.data;
    const fileReader = new FileReader();
    fileReader.readAsArrayBuffer(blob)
    fileReader.onload = (e) => {

      console.log('Unzipping file...');
      let strContent = pako.inflate(e?.target?.result);
      let sizeBuffer = strContent.length;
      // check correct nifti header after extract raw bytes from gzip
      const headTemplate = [0x00, 0x00, 0x01, 0x5c];
      let correctHead0 = true;
      for (let i = 0; i < 4; i++) {
        if (strContent[i] !== headTemplate[i]) {
          correctHead0 = false;
        }
      }
      let correctHead1 = true;
      for (let i = 0; i < 4; i++) {
        if (strContent[i] !== headTemplate[3 - i]) {
          correctHead1 = false;
        }
      }
      if (!correctHead0 && !correctHead1) {
        console.log('Wrong nifi header, cant read gzipped file');
        return;
      }
      console.log('ungzip done with ' + sizeBuffer.toString() + ' bytes. Correct nifti header detected');

      console.log('UiOpenMenu. onFileReadSingleBuffer ...');
      // console.log(`file content = ${strContent.substring(0, 64)}`);
      // console.log(`onFileContentRead. type = ${typeof strContent}`);
      const volumeSet = new VolumeSet({});
      // add empty [0]-th volume in set to read single file
      volumeSet.addVolume(new Volume({}))
      const callbackProgress = () => { };
      const callbackCompleteSingleDicom = () => { };
      const callbackComplete = (errCorde: string) => {

        console.assert(volumeSet instanceof VolumeSet, "finalizeSuccessLoadedVolume: should be VolumeSet");
        console.assert(volumeSet.getNumVolumes() >= 1, "finalizeSuccessLoadedVolume: should be more or 1 volume");
        const indexVol = 0;

        const vol = volumeSet.getVolume(indexVol);
        console.assert(vol !== null, "finalizeSuccessLoadedVolume: should be non zero volume");

        if (vol.m_dataArray !== null) {
          console.log(`success loaded volume from ${event.data.scanId}`);
          if (NEED_TEXTURE_SIZE_4X) {
            vol.makeDimensions4x();
          }
          // invoke notification
          // send update (repaint) if was loaded prev model
          const tex3d = new Texture3D();
          tex3d.createFromRawVolume(vol);

          ctx.postMessage({status: 'start', text3d: JSON.stringify(tex3d)});
          volumeSet.m_volumes.forEach(v => {
            // We have to separate the volumeset data because stringify takes too long
            const data = v.m_dataArray;
            // Because this is in a web worker we cannot pass functions, which all of these objects have.
            // Stringify gets rid of the functions.
            v.m_dataArray = null;
            ctx.postMessage({status: 'vol', volume: JSON.stringify(v), data});
          });

          ctx.postMessage({status: 'end', volumeSet: JSON.stringify(volumeSet)});
        };
      }
      volumeSet.readFromNifti(strContent, callbackProgress, callbackComplete);
    }
  }
  else ctx.postMessage({ status: 'fail' });

});
